body {
  margin: 0;

  background-color: #f7f7f7;
  font-family: "Rubik", sans-serif;
}
/* Nav ##########################*/
nav {
  position: sticky;
  top: 0;
  width: 100%;
  height: 64px;
  background-color: rgb(95, 170, 82);
  text-align: center;
  color: white;
  z-index: 2;
}
nav h4 {
  float: left;
  margin-left: 10px;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 0;
}
nav h6 {
  float: right;
  margin-right: 20px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 26px;
}
nav input {
  margin-top: 16px;
  padding: 8px;
  border: none;
  border-radius: 8px;
  width: 70px;
  cursor: pointer;
  transition: all 0.35s ease-in-out;
  float: left;
  margin-left: 50px;

  background-image: url(/assest/NavIcon/search.png);
  background-position: 5px 5px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  padding-left: 30px;
}

#basket {
  position: absolute;
  right: 20px;
  fill: white;
  margin: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: -1;
  cursor: pointer;
}
::placeholder {
  font-style: italic;
}

nav input:hover {
  width: 150px;
}

nav input:focus {
  transform: scale(1.04);
}

/*ads####################################*/
#ads {
  width: 100%;
  height: 50px;
  /* background-color: rgb(68, 102, 117);
   */
  background-color: rgba(56, 125, 122, 1);
}
.adsTextElement {
  position: relative;
  display: flow-root;
  color: white;
  margin: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
/* slider ############### */
#sliderContainer {
  overflow: hidden;
  white-space: nowrap;
  margin: auto;
  position: relative;
  user-select: none;
  /* background-color:rgba(2, 195, 154, 1); */

  margin-bottom: 15px;
  margin: 20px;
  padding-top: 3px;
  padding-bottom: 5px;
}
.sliderElement {
  width: 100%;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 97%;
  /* border-radius: 62px; */
  display: inline-block;
  transition: all 1s ease-in-out;
  padding: 0;
  /* background-color: white; */
  /* background-color:rgba(2, 195, 154, 1); */
  background-color: #f7f7f7;
  margin: auto;
  /* margin-bottom: 5%; */
  margin-top: 10px;
}

#sliderContainer:hover #leftArrow {
  opacity: 1;
}
#sliderContainer:hover #rightArrow {
  opacity: 1;
}
.arrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  opacity: 0;
  fill: white;
  stroke: green;
  stroke-width: 0.4px;
  cursor: pointer;
  transition: all 0.5s;
}
#leftArrow {
  left: 25px;
}
#rightArrow {
  right: 25px;
}
#dots {
  opacity: 0;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translate(-50%);
  transition: all 0.5s;
}
#dots svg {
  fill: green;
  cursor: pointer;
}
#sliderContainer:hover #dots {
  opacity: 1;
}
/* EmpfehlungenContainer */
.EmpfehlungenContainer {
  position: relative;
  width: 100%;
  stroke-width: 1px;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 13px;
  padding-bottom: 1px;
}
.EmpfehlungenContainer:hover .productArrow {
  opacity: 1;
}
.productArrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 50%;
  background-color: rgba(2, 195, 154);
  fill: white;
  opacity: 0;
  z-index: 1;
  transition: opacity 0.5s;
}
.productLeftArrow {
  left: 0;
}
.productRightArrow {
  right: 0;
}

.EmpfehlungenContainerTitle {
  margin-top: 3px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 5px;
  font-family: "Rubik", sans-serif;

  /* background-color:rgba(255, 120, 90, 1); */
  /* background-color: rgba(254, 216, 102, 0.322);  */
  /* background-color: rgba(56, 125, 122, 1);; */
  /* background-color: #5d5d5d5d; */

  padding: 12px;
  border-radius: 10px;
  font-size: large;
  text-align: left;
}

/*productContainer*/

.productContainer {
  width: 285px;
  height: 400px;
  background-color: #5d5d5d;
  margin: 10px;
  padding-bottom: 5px;
  border: solid 1px rgb(207, 203, 202);
  border-radius: 10px;
  padding: 20px;
  display: inline-block;
  /* background-color: rgba(255, 250, 234, 0.513); */
  background-color: white;
  position: relative;
  box-sizing: border-box;
  transition: all 0.5s;
}
.productContainer:hover {
  border: none;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  transform: scale(1.01);
}
.productContainer * {
  margin: 2px;
}

.productContainer h1 h2 h3 h4 h5 h6 {
  color: rgb(39, 36, 34);
  font-size: 16px;
  line-height: 24px;
  font-family: SourceSansPro, Arial, sans-serif;
  font-weight: 700;
  letter-spacing: normal;
}
.productPic {
  width: 100%;
  height: 220px;
  margin-bottom: 20px;
  background-size: cover;
}
.action h4 {
  margin: 2px;
  font-size: x-large;
  color: #f7f7f7;
}
.action {
  position: absolute;
  top: 1px;
  left: 1px;
  /* background-color: red; */
  border-radius: 10px;
  padding: 5px;
}
#productRating {
  margin-left: 10px;
}
#ratespan {
  margin-left: 10px;
  margin-top: 10px;
  font-size: 8px;
}
#productName {
  margin-left: 10px;
  font-size: large;
  overflow: hidden;
  height: 43px;
  word-break: break-all;
  white-space: initial;
  margin-bottom: 3px;
}

#productPrice {
  margin-left: 10px;
  font-size: large;
  color: red;
}
#productNote {
  margin-left: 10px;
  margin-top: 0;
  font-size: medium;
  margin-bottom: 10px;
  overflow: hidden;
}
.ProductPricing {
  white-space: nowrap;
  margin-left: 10px;
}
.ProductPricing h5 {
  display: inline;
}
#productOldPrice {
  text-decoration: line-through;
}

/* SideNavBar *************************************/
#SideNavBarContainer {
  position: fixed;
  left: 0;
  top: 0;
  width: 220px;
  height: 100vh;
  background-color: teal;
  transition: left 0.5s;
  touch-action: none;
  z-index: 5;
}
#bluredBG {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  touch-action: none;
  background-color: rgba(0, 0, 0, 0.9);
  display: none;
  opacity: 0;
  transition: all 0.5s;
}
#hamButton {
  float: left;
  margin-top: 18px;
  margin-left: 10px;
  fill: white;
  cursor: pointer;
}
#sideNavBarClose {
  position: absolute;
  top: 10px;
  right: 10px;
  fill: white;
  cursor: pointer;
}
#sideNavBarContent {
  width: 100%;
  height: 90vh;
  margin-top: 10vh;
}
#sideNavBarContent h5 {
  text-align: left;
  margin: 20px;
  text-align-last: justify;
  cursor: pointer;
  user-select: none;
}
